import React, { useEffect } from 'react';
import { Container, Stack, Center } from '@chakra-ui/layout';
import { StaticImage } from 'gatsby-plugin-image';
import { useViewportScroll } from 'framer-motion'
import { navigate } from 'gatsby';

import Box from '../../components/Box';
import Text from '../../components/Text';
import Flex from '../../components/Flex';
import Button from '../../components/Button';
import BackgroundImage from '../../components/BackgroundImage';
import { responsive } from '../../contexts/responsive';
import useHeader from '../../contexts/header/useHeader';

import logo_bg from './logo-bg.png'

const team = [
  {
    logo: <StaticImage src="TEIA.png" />,
    name: '發起團隊 | 台灣環境資訊協會',
    text: '我們致力於提供多元且真實的環境資訊，讓民眾面臨價值選擇時有足夠的依準。我們投入棲地守護與環境友善行動，結合公民、企業、政府等個人與組織，從山林到海洋、生活到倡議都涵納在我們的行動中。\n意識到珊瑚礁生態系的重要性，我們從2009年起不間斷進行珊瑚礁監測，我們相信，藉由「珊瑚礁體檢」，讓一般潛水員將他們親眼所見轉化成科學數據，潛水活動也能成為具有海洋愛護的力量，讓社會大眾、所有關心海洋的人都能更接近、更了解水下世界的美麗和窘境。\n此次我們將公布過去十二年所搜集的成果，期望作為未來海洋政策建議，也讓大家都能夠看到台灣海面下的生態過去至今到底發生了什麼事？'
  },
  {
    logo: <StaticImage src="relab.png" />,
    name: '製作團隊 | RE：LAB',
    text: '「每到夏天，總會看見珊瑚白化的相關訊息，但夏天過了難道就恢復了嗎？」\n這是促使 RE:LAB 開啟和環資合作這個計劃的開端，在實際參與潛水體驗、上過課程、閱讀資料之後，才發現這片海底之下的美景，潛藏了許多未知且值得被重視的美好。\n身為一個致力於將資訊轉譯成有趣故事和體驗的團隊，希望透過這個計畫邀請你一起來重新認識和我們共處的這些生態系的美麗與哀愁。'
  },
]

const footer = [
  { contact: '參與體檢、資料相關問題請聯繫', name: '台灣環境資訊協會', href: 'mailto:service@e-info.org.tw' },
  { contact: '網站製作與維護相關問題請聯繫', name: 'RE:LAB', href: 'mailto:hi@relab.cc' },
  { contact: '如果你想參與計畫', name: '我要報名體檢志工', href: 'https://teia.tw/ocean_lastestpost/2021%e7%8f%8a%e7%91%9a%e7%a4%81%e9%ab%94%e6%aa%a2%e8%a1%8c%e7%a8%8b-2/' },
]

const threshold = 0.01

const AboutPage = () => {
  const { setHeaderColor } = useHeader()
  const { scrollY } = useViewportScroll()
  useEffect(() => setHeaderColor('black') ,[])
  useEffect(() => {
    let lastY;
    let prevUp
    let timer

    const listener = (e) => {
      const currentY = e.touches?.[0].clientY;
      if (currentY > lastY || e.deltaY < 0) {
        if (prevUp) {
          if (scrollY.get() < threshold) {
            timer = setTimeout(() => {
              if (prevUp && scrollY.get() < threshold) {
                navigate('/#epilogue')
              }
            }, 1000)
            // console.log('move!!')
          }
        }
        prevUp = true
      } else {
        if (timer) clearTimeout(timer)
        prevUp = false
      }
      lastY = currentY
    }

    document.addEventListener('mousewheel', listener)
    document.addEventListener('touchmove', listener)
    return () => {
      document.removeEventListener('mousewheel', listener)
      document.removeEventListener('touchmove', listener)
      if (timer) clearTimeout(timer)
    }
  }, [scrollY])
  return (
    <Box bg="custom.aboutBg" overflow={'hidden'}>
      <Box className="normalScroll">
        <Container as={Stack}>
          <Center height="100vh">
            <Box width="100%" mx="auto" height={responsive('40%', '70%')} bg="black">
              <Center height={'100%'} color="white">影片</Center>
            </Box>
          </Center>
          <Center id="report" height="100vh">
            <Box width="100%" mx="auto" height={responsive('40%', '70%')} bg="black">
              <Center height={'100%'} color="white">報告</Center>
            </Box>
          </Center>
        </Container>
        <Box bg="custom.skyBlue" py={responsive('2.75em', '9em')} px={responsive('2.25em', '12.5em')}>
          <Text.Bold fontSize={responsive('1.5em', '3.5em')}>製作團隊介紹</Text.Bold>
          {team.map((d, i) => (
            <Flex key={i} alignItems="center" borderTop={i && '1px solid'} borderColor="custom.divideBlue" pt={i && responsive('2em', '5em')} mt={responsive('2em', '5em')} flexDirection={responsive('column', 'row')}>
              <Box width={responsive('80%', '25%')}>
                <BackgroundImage src={logo_bg} ratio={464 / 415}>
                  <Box.AbsCenter width="60%">
                    {d.logo}
                  </Box.AbsCenter>
                </BackgroundImage>
              </Box>
              <Box flex={1} ml={responsive(0, '4.5em')} mt={responsive('1.5em', 0)}>
                <Text.Bold fontFamily="noto sans TC" fontSize={responsive('1.375em', '2.5em')}>{d.name}</Text.Bold>
                <Text mt={responsive('0.5em', '1em')} fontSize={responsive('1em', '1.75em')} whiteSpace="pre-wrap">{d.text}</Text>
              </Box>
            </Flex>
          ))}
        </Box>
        <Box id="contact" bg="custom.oceanBlue" py={responsive('4.625em', '5.625em')} px={responsive('3.375em', '11em')}>
          <Flex flexWrap="wrap" justifyContent={'center'}>
            {footer.map((d, i) => (
              <Box
                px={responsive(0, '3em')}
                pb={responsive('2.25em', '2em')}
                pt={responsive(i && '1.75em', '2em')}
                borderLeft={i && responsive('none', '1px solid white')}
                borderTop={i && responsive('1px solid white', 'none')}
                width={responsive('100%', 1 / 3)}
                textAlign="center"
                key={i}
              >
                <Text fontSize={responsive('1.125em', '1.75em')} color="white">{d.contact}</Text>
                <Button href={d.href} width="100%" mt={responsive('1rem', '0.6875em')} fontSize={responsive('1.5em', '2em')}>
                  {d.name}
                </Button>
              </Box>
            ))}
          </Flex>
          <Center pt={responsive('8.75em', '5.625em')}>
            <Box width={responsive('12em', '16em')}>
              <StaticImage src="footer_logo.png" />
            </Box>
          </Center>
        </Box>
      </Box>
    </Box>
  )
};

export default AboutPage;
